import React from "react";
import { Modal } from "react-bootstrap";
import { Link } from "react-router-dom";
import styled from "styled-components";

const ExportPdfModal = ({ className, isOpen, setOpen }) => {
  return (
    <Modal show={isOpen} className={className} contentClassName="modal__custom">
      <div className="modal-header modal__custom__header">
        <button
          type="button"
          className="btn-close modal__custom__button--close"
          data-bs-dismiss="modal"
          aria-label="Close"
          onClick={() => setOpen?.(false)}
        ></button>
      </div>
      <div className="modal-body">
        <p className="fs__title">Báo cáo của bạn đang được xuất</p>
        {/* <p className="fs__title mt-3">VIETCOMBANK</p>
        <p className="d-flex justify-content-between">
          Số tài khoản: <span>19030001119333</span>
        </p>
        <p className="d-flex justify-content-between">
          Số điện thoại: <span>0123456789</span>
        </p>
        <p className="d-flex justify-content-between">
          Tên đầy đủ: <span>Nguyễn Văn A</span>
        </p>
        <p className="d-flex justify-content-between">
          Số tiền: <span>499.000 VND</span>
        </p> */}
        <p
          className="fw-bolder fs__text border-top pt-3"
          style={{
            borderColor: "#ffc367 !important",
            borderWidth: "2px !important",
          }}
        >
          Lưu ý:
        </p>
        <p className="fs__text">
          - Số lượt xuất pdf của bạn sẽ bị giảm khi nhấn vào nút "Xuất pdf"
        </p>
        <p className="fs__text">
          - Bạn có thể tăng lượt xuất pdf bằng việc nhập mã hoặc{" "}
          <Link to="/agency-policy">trở thành đại lý</Link>
        </p>
      </div>
      <div className="modal-footer modal__custom__footer">
        <button
          type="button"
          className="btn button-secondary--outline"
          data-bs-dismiss="modal"
          onClick={() => setOpen?.(false)}
        >
          Đóng
        </button>
        <button type="button" className="btn button-secondary" onClick={() => setOpen?.(false)}>
          Xong
        </button>
      </div>
    </Modal>
    // <div
    //   className={`${className} modal show`}
    //   id="staticBackdrop"
    //   data-bs-backdrop="static"
    //   data-bs-keyboard="false"
    //   tabIndex="-1"
    //   aria-labelledby="staticBackdropLabel"
    //   aria-hidden="true"
    // >

    // {/* </div> */}
  );
};

export default styled(ExportPdfModal)`
  .modal__custom {
    background-color: #fafafa;
    color: black;
    &__header {
      border: none;
    }
    &__footer {
      border: 0;
    }
    &__button--close {
      color: #fafafa !important;
      font-weight: bolder;
      &:hover {
        color: $color-text-title;
      }
    }
  }
`;
